<template>
    <hr />
    <div class='process-wrapper'>
        <h2>{{ this.title }}</h2>
    </div>

    <div class="parent">
        <div class="div1">
            <div class="title">
                <h3 class="top-sub">Key Metrics</h3>
                <h3>Summary</h3>
            </div>
            <div class="title">
            <div class="ppmetrics-container">
                <div class="metrics-wrapper">
                    <p class="metrics-num"> {{ this.agtNum }} </p>
                    <p class="metrics-title">agreements</p>
                </div>
                <div class="metrics-wrapper">
                    <p class="metrics-num"> {{ this.actorNum }} </p>
                    <p class="metrics-title">actors</p>
                </div>
                <div class="metrics-wrapper">
                    <p class="metrics-num"> {{ this.time }} </p>
                    <p class="metrics-title">timespan</p>
                </div>
            </div>
            </div>
        </div>

        <div class="div2">
                <div class="title">
                    <h3 class="top-sub">Legends</h3>
                    <h3>Entity and relations</h3>
                </div>
                <el-affix :offset="0">
                    <div class="legend-box">
                    <legends />
                    </div>
                </el-affix>
        </div>

        <div class="div3" style="position: relative;">
            <div class="network">
            <div class="title">
                <h3 class="top-sub">Networks</h3>
                <h3>Actors agreement network view</h3>
                <p class="chart-description">
                    Forced layout of actor network.
                    <br />
                    <b>Hover</b> to see name of the dot.
                    <br />
                    <b>Click</b> to see agreement and actor connections.
                </p>
            </div>
            <div id="network" style="width: 100%;"></div>
            <div class="title" style="position: absolute; bottom: 0px; z-index: 10;">
                <el-button round size="small" @click="clickNetwork()">expand to full view</el-button>
                <el-button round size="small" @click="exportNetwork()" class="export-button">
                    download png<el-icon><Download /></el-icon>
                </el-button>
            </div>
            </div>
            <el-dialog
                align-center
                v-model="dialogVisible"
                title="Actors agreement network view"
                width="95%"
                >
                <network
                    ref="network" 
                    :title="this.title" 
                    :actorList="this.actorList" 
                    :agreementList="this.agreementList"/>
            </el-dialog>
        </div>

        <div class="div4" style="position: relative;">
            <div class="list">
                <div class="title">
                    <h3 class="top-sub">List</h3>
                    <h3>Actors list view in each agreement</h3>
                    <p class="chart-description">
                        List of actors associated to agreements, no particular relationship related.
                    </p>
                </div>
                <div id="list" style=" width:100%;"></div>
            </div>
            <div class="title" style="position: absolute; bottom: 0px; z-index: 10;">
                <el-button round size="small" @click="clickMatrix()">expand to adjacency matrix</el-button>
                <el-button round size="small" @click="exportList()" class="export-button">
                    download png<el-icon><Download /></el-icon>
                </el-button>
                </div>

            <el-dialog
            align-center
            v-model="matrixVisible"
            title="Adjacency matrix view"
            width="95%"
            >
            <adjacencymatrics
                ref="adjacencymatrics" 
                :title="this.title"
                :agtNum="agtNum"
                :actorNum="actorNum"/>
            </el-dialog>
        </div>


        <div class="div5" id="div5" style="position: relative;">
            <div class="jigsaw">
            <div class="title">
                <h3 class="top-sub">Jigsaw</h3>
                <h3>Agreements and Actors in columns</h3>
                <p class="chart-description">
                    Agreements ordered by date listed on left column, actors on the right.
                </p>
            </div>
            <div id="jigsaw" style="width: 100%;" ></div>
            </div>
            <div class="title" style="position: absolute; bottom: 0px; z-index: 10;">
                <el-button round size="small" @click="exportJigsaw()" class="export-button">
                    download png<el-icon><Download /></el-icon>
                </el-button>
                </div>
        </div>
    </div>



</template>

<script>
import legends from '@/components/legends.vue'
import * as svg from 'save-svg-as-png'

import network from '../components/network.vue'
import adjacencymatrics from '../components/adjacencymatrics.vue'


export default {
    components: {legends, network, adjacencymatrics},

    props: ["data"],

    data() {
        return {
            title: '',
            agtNum: 0,
            actorNum: 0,
            time: '',
            actorList: [],
            agreementList: [],
            dialogVisible: false,
            matrixVisible:false
        }
    },

    methods: {
        clickNetwork() {
            this.dialogVisible = true
            this.$nextTick(() => {
                this.$refs.network.render()
            })
        },

        clickMatrix() {
            this.matrixVisible = true
            this.$nextTick(() => {
                this.$refs.adjacencymatrics.render()
            })
        },

        exportNetwork() {
            svg.saveSvgAsPng(document.querySelector('.network svg'), "network.png");
        },
            
        exportList() {
            svg.saveSvgAsPng(document.querySelector('.list svg'), "list.png");
        },

        exportJigsaw() {
            svg.saveSvgAsPng(document.querySelector('.jigsaw svg'), "jigsaw.png");
        }
    },

    computed:{
		get(){
			return this.data;
		}
	},

	watch:{
		get(val){
            this.title = val.pp
            this.agtNum = val.agtNum
            this.actorNum = val.actorNum
            this.time = val.time
            this.actorList = val.actorList
            this.agreementList = val.actors

		}
	}
}

</script>