<template>
    <div class="legend-container">
        <div>
            <p class="network-legends">Agreement</p>
            <svg id="agt_legend"></svg>
        </div>
        
        <div>
            <p class="network-legends">Signatory types</p>
            <svg id="edge_legend"></svg>
        </div>

        <div>
            <p class="network-legends">Actor groups</p>
            <svg v-if="this.width >= 1200" id="entity_legend"></svg>
            <svg v-else-if="this.width < 1200 && this.width >= 300" id="test_legend"></svg>
        </div>
  </div>
</template>

<script>
    import * as d3 from "d3";

    export default {
    data() {
        return {
            timelienLegends:["other agreements"],
            width: 0
        };
    },
    
    mounted() {
        this.render()
        //get window
        window.addEventListener('load', this.render)
        window.addEventListener('resize', this.debounce(this.render, 100))
    },

    methods: {
        twoLegend(x) {
            if (x.matches) { // If media query matches
                document.body.style.backgroundColor = "yellow";
            } 
            else {
                document.body.style.backgroundColor = "pink";
            }
        },

        debounce(operate, delay) {
            let time = null
            let timer = null
            let newTime = null
            function task() {
                newTime = +new Date()
                if( newTime - time < delay ){
                    timer = setTimeout(task, delay)
                }
                else {
                    operate()
                    timer = null
                }
                time = newTime
            }
            return function () {
                time = +new Date()
                if(!timer){
                    timer = setTimeout(task, delay)
                }
            }
        },
        
        getWiddow() {
            var w = window.outerWidth;
            this.width = w
            return w
        },

        async render() {
            await this.getWiddow()
            await this.renderLegend()
        },

        renderLegend() {
        const width = 220
        const rectWidth = 16
        const dotRadius = 8

        const entities = [
            "Country/State",
            "Entity",
            "IGO",
            "NGO",
            "Political Party",
            "Armed Organization",
            "Military",
            "State Coalition",
            "Umbrella",
            "Other"
        ];

        let timelineList = ["Agreements in selected process", "All other agreemenets"];
        let timelineColors = ["#60A18B", "#D9D9D9"];
        var timelineColorScale = d3.scaleOrdinal()
                                    .domain(timelineList)
                                    .range(timelineColors)

        var timelineSvg = d3.selectAll("#timeline_legend")
                                .attr("width", 800)
                                .attr("height", 50)
                                

        timelineSvg.selectAll("rect")
                    .data(timelineList)
                    .enter()
                    .append('rect')
                        .attr("x", 20)
                        .attr("y", (d,i) => (i*25))
                        .attr("width", 18)
                        .attr("height", 18)
                        .style("fill", d => timelineColorScale(d));

        timelineSvg.selectAll("labels")
                    .data(timelineList)
                    .enter()
                    .append('text')
                        .attr("class", "legend-labels")
                        .attr("x", 50)
                        .attr("y", (d,i) => (i*25 + 15))
                        .style("fill", "black")
                        .text(d => d);

        const agt = ["Peace Agreement"];

        const signatory = ["Party", "Third Party"];
        const sigColor = ["#C1C1C1","#D3D3D3"];

        var sigColorScale = d3.scaleOrdinal()
                            .domain(signatory)
                            .range(sigColor);

        var svg = d3.selectAll("#entity_legend")
                    .attr('width', width)
                    .attr('height', 250);

        var colorRange = [
                "#EAC05B",
                "#D97144",
                "#E8A5D5",
                "#7FAADC",
                "#714FBA",
                "#9EB449",
                "#CEAC9D",
                "#F6F5BF",
                "#568AA4",
                "#CDD2CC"
        ];

        var color = d3.scaleOrdinal()
                    .domain(entities)
                    .range(colorRange);
        
        //entities in media query
        svg.selectAll("dots")
            .data(entities)
            .enter()
            .append("circle")
                .attr("cx", 30)
                .attr("cy", function(d,i){ return 10 + i*24})
                .attr("r", dotRadius)
                .style("fill", d => color(d));
   
        svg.selectAll("labels")
            .data(entities)
            .enter()
            .append("text")
            .attr("x", 50)
            .attr("y", function(d,i){ return 10 + i*24})
            .style("fill", "black")
            .text(d => d)
            .attr("text-anchor", "left")
            .attr("class", "legend-labels")
            .style("alignment-baseline", "middle");

        //the second legend
        var svgH = d3.selectAll("#test_legend")
                     .attr('height', 120)
                     .attr('width', 350)
                    
        var g = svgH.append('g')
                    .attr('class', 'legend-groups')
                    .attr("transform", function(d, i) {
                                    return "translate(0,0)";
                            })

        g.selectAll('g')
            .data(entities)
            .enter()
            .append('circle')
            .attr("cx", function(d, i){
                if (i <= 4 ) { return 30 }
                else { return 200 }
            })
            .attr("cy", function(d,i){ 
                if (i <= 4) { return 10 + i*24}
                else { return i*25 - 115}

            })
            .attr("r", dotRadius)
            .style("fill", d => color(d));
        
        g.selectAll('g')
            .data(entities)
            .enter()
            .append("text")
            .attr("x", function(d, i){
                if (i <= 4 ) { return 50 }
                else { return 220 }
            })
            .attr("y", function(d,i){ 
                if (i <= 4) { return 10 + i*24}
                else { return i*25 - 115}
            })
            .style("fill", "black")
            .text(d => d)
            .attr("text-anchor", "left")
            .attr("class", "legend-labels")
            .style("alignment-baseline", "middle");
                            
        
        //agt_legend
        var svgAgt = d3.selectAll("#agt_legend")
                    .attr('width', width)
                    .attr('height', 50);

        svgAgt.selectAll("rect")
                .data(agt)
                .enter()
                .append("rect")
                    .attr("x", 22)
                    .attr("y", function(d,i){ return 0 + i*25})
                    .attr("width", rectWidth)
                    .attr("height", rectWidth)
                    .style("fill", "#60A18B")

        svgAgt.selectAll("labels")
            .data(agt)
            .enter()
            .append("text")
            .attr("x", 50)
            .attr("y", function(d,i){ return 10 + i*25})
            .style("fill", "black")
            .text(d => d)
            .attr("text-anchor", "left")
            .attr("class", "legend-labels")
            .style("alignment-baseline", "middle")

        var svgSig = d3.selectAll("#edge_legend")
                    .attr('width', width)
                    .attr('height', 60);

        svgSig.selectAll("rect")
                .data(signatory)
                .enter()
                .append("rect")
                    .attr("x", 22)
                    .attr("y", function(d,i){ return 6 + i*25})
                    .attr("width", 30)
                    .attr("height", function(d,i){ return 8 / (i+1) })
                    .style("fill", d => sigColorScale(d))

        svgSig.selectAll("labels")
            .data(signatory)
            .enter()
            .append("text")
            .attr("x", 60)
            .attr("y", function(d,i){ return 10 + i*25})
            .style("fill", "black")
            .text(d => d)
            .attr("text-anchor", "left")
            .attr("class", "legend-labels")
            .style("alignment-baseline", "middle")
        }
    }
    };
</script>


<style scoped>

